    Round1New: function (val, row) {
        var s = "";
        if (val != null) {
            let f = parseFloat(val);
            if (isNaN(f) || f <= 0) {
                return "";
            }
            f = Math.round(val * 10) / 10;
            s = f.toString();
            let rs = s.indexOf('.');
            if (rs < 0) {
                rs = s.length;
                s += '.';
            }
            while (s.length <= rs + 1) {
                s += '0';
            }
        }
        return s;
    },
    toDecimal: function (x, dotnum) {

        if (x) {
            if (isNaN(x)) {
                return "";
            }
            let num = parseFloat(x);

            let f = Math.round(num * Math.pow(10, dotnum)) / Math.pow(10, dotnum);
            let s = f.toString();
            let rs = s.indexOf('.');
            if (rs < 0) {
                rs = s.length;
                s += '.';
            }
            while (s.length <= rs + dotnum) {
                s += '0';
            }
            //console.log(x + "," + s);
            return s;
        }
        return "";
    },

