    /*UTC*/
    ToUTC: function (date) {
        date = date.replace(/-/g, "/");
        let dt = new Date(date); //StringToDate(date);

        return Date.UTC(dt.getFullYear(), dt.getMonth(), dt.getDate(), dt.getHours(), dt.getMinutes());
    },
